@import "https://fonts.googleapis.com/css2?family=Caveat&family=Yanone+Kaffeesatz:wght@700&display=swap";
:root {
  --bg-color: #383634;
  --bg-keycap: #2c2c2a;
  --pri-yellow: #e9d97d;
  --pri-light: #dddfe0;
  --pri-dark: #171717;
  --pri-red: #bb405c;
  --pri-cyan: #5ed7d3;
  --h: 110px;
  --w: 110px;
  --d: 40px;
  --rotate-deg: 30deg;
  --margin-shape: -30px;
  --border-color: #2b2c2a;
}

body {
  background-color: var(--bg-color);
  color: var(--pri-light);
  overscroll-behavior: none;
  height: 100vh;
  font-family: Yanone Kaffeesatz, sans-serif;
}

.trigger {
  width: 0;
  height: 18px;
  background: var(--pri-light);
  position: absolute;
  top: 75%;
}

.trigger-2 {
  position: absolute;
  left: 50%;
  overflow: visible !important;
}

.theLine {
  stroke-dasharray: 0 999999;
}

.container {
  overscroll-behavior: none;
  width: 300%;
  height: 100%;
  flex-wrap: nowrap;
  display: flex;
}

.sections {
  width: 100%;
}

h2 {
  color: var(--pri-red);
  z-index: 1;
  text-shadow: 2px 2px 20px #0000001a;
  margin-bottom: -20px;
  font-family: Caveat, cursive;
  font-size: 4em;
  position: relative;
}

.title-wrapper {
  text-align: center;
  margin-bottom: -110px;
}

#title, #title2 {
  justify-content: center;
  align-items: center;
  display: flex;
}

h1 {
  margin: 0;
  font-size: 10em;
  line-height: .8;
}

.sections-1 {
  perspective: 1000px;
  max-width: 700px;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
  position: relative;
}

.star {
  opacity: 0;
  position: absolute;
}

.star img {
  width: 35px;
  height: 35px;
}

.star.left {
  animation-delay: 1s;
  left: 5%;
}

.star.right {
  top: 25%;
  right: 5%;
}

.star.top {
  animation-delay: 2s;
  top: 10%;
  left: 35%;
}

.board {
  width: 300px;
  height: 300px;
  background-color: var(--pri-light);
  will-change: transform;
  transform-style: preserve-3d;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  transform: translate3d(0, 0, 0)scale3d(1, 1, 1)skew(0deg)rotateX(70deg)rotateY(0)rotateZ(320deg);
}

.keycap {
  cursor: pointer;
  transform: translateZ(var(--d));
  will-change: transform;
  transform-style: preserve-3d;
  transition: transform .3s ease-out;
  display: flex;
  position: relative;
}

.keycap:hover {
  transform: translateZ(calc(var(--d) * .5) );
}

.wrapper {
  max-width: calc(var(--w) * 2 + 12px);
  will-change: transform;
  transform-style: preserve-3d;
  background: #2c2c2a;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 8px;
  display: flex;
}

.trapezoid {
  border: 1px solid var(--border-color);
  width: var(--d);
  height: var(--h);
  background: var(--pri-yellow);
}

.trapezoid.full-height {
  height: 100%;
}

.trapezoid.full-width {
  width: 100%;
  height: var(--d);
}

.trapezoid.light {
  background: var(--pri-light);
}

.trapezoid.mid {
  color: #2e3229;
  text-align: center;
  font-size: 7em;
  line-height: 1.05;
  width: var(--w) !important;
  height: var(--h) !important;
}

.trapezoid.red {
  background: var(--pri-red);
}

.trapezoid.dark {
  background: var(--pri-dark);
}

.trapezoid.cyan {
  background: var(--pri-cyan);
}

.trapezoid.left {
  transform-origin: 0;
  position: absolute;
  left: 0;
  transform: rotateY(90deg);
}

.trapezoid.bottom {
  transform-origin: bottom;
  position: absolute;
  bottom: 0;
  transform: rotateX(90deg);
}

.trapezoid.right {
  transform-origin: 100%;
  position: absolute;
  right: 0;
  transform: rotateY(-90deg);
}

.trapezoid.top {
  transform-origin: top;
  position: absolute;
  transform: rotateX(-90deg);
}

.trapezoid.cat {
  background-image: url("https://cdn-icons-png.flaticon.com/512/1581/1581594.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 70% 70%;
}

.trapezoid.inu {
  background-image: url("https://cdn-icons-png.flaticon.com/512/7016/7016538.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 150% 150%;
}

.center {
  will-change: transform;
  transform-style: preserve-3d;
  position: relative;
}

.center .trapezoid {
  width: var(--w);
  height: var(--d);
}

@keyframes example {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.star {
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

